/* Existing styles */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* New styles for personal website */
body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header {
  background-color: #282c34;
  color: white;
  text-align: center;
  padding: 1rem;
}

header h1 {
  margin: 0;
  font-size: calc(20px + 2vmin);
}

nav {
  margin-top: 1rem;
}

nav button {
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 1rem;
  margin: 0 0.5rem;
  padding: 0.5rem 1rem;
  transition: background-color 0.3s;
}

nav button:hover, nav button.active {
  background-color: #61dafb;
  color: #282c34;
}

main {
  flex: 1;
  padding: 2rem;
  max-width: 800px;
  margin: 0 auto;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

footer {
  background-color: #282c34;
  color: white;
  text-align: center;
  padding: 1rem;
  margin-top: auto;
}

.content h2 {
  color: #282c34;
}

.content ul {
  padding-left: 1.5rem;
  text-align: left;
}

.content li {
  margin-bottom: 0.5rem;
}

/* Responsive design */
@media (max-width: 600px) {
  nav button {
    display: block;
    width: 100%;
    margin: 0.5rem 0;
  }
}